<template>
  <view class="new-maint-record padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="新增维修日志"></u-navbar>
    <view>
      <nnkjc-detail-group>
        <nnkjc-detail-item
            detailTitle="维修日志"
            :align="'left'"
            :gap="false"
        >
          <view slot="content">
            <u-input type="textarea"
                     :height="200"
                     :maxlength="500"
                     placeholder="请填写维修过程以及安全措施"
                     placeholder-style="color: #D4D4D5; line-height: 1; font-size: 14px;"
                     v-model="form.content"/>
          </view>
        </nnkjc-detail-item>

        <view style="padding-bottom: 10px; border-bottom: 1px solid #f1f1f1;">
          <u-upload
              width="100"
              height="100"
              uploadText=""
              :action="action"
              customBtn
              @on-success="onSuccess"
              @on-remove="onRemove"
              :header="header"
              :form-data="formData"
              :file-list="fileList">
            <view class="add-btn" slot="addBtn">
              <u-icon name="camera-fill"></u-icon>
            </view>
          </u-upload>
        </view>

        <nnkjc-detail-item
            detailTitle="维修结果"
            :align="'left'"
            :gap="false"
        >
          <view slot="content">
            <u-radio-group v-model="form.finishFlag">
              <u-radio
                  @change="resultChange"
                  :label-size="'14px'"
                  :active-color="'#24a27c'"
                  v-for="(res, index) in resultList" :key="index"
                  :name="res.name"
              >
                {{res.title}}
              </u-radio>
            </u-radio-group>
          </view>
        </nnkjc-detail-item>
      </nnkjc-detail-group>
    </view>
    <view class="margin-normal radius-normal">
      <u-button
          ripple
          type="success"
          :loading="loading"
          @click="$u.debounce(submitForm, 500)">确认提交
      </u-button>
    </view>

    <u-toast ref="uToast"/>
  </view>
</template>

<script>
  import CONFIG from "../../config";

  export default {
    name: "new-maint-record",
    data() {
      return {
        loading: false,
        background: {
          'background': '#f2f2f8'
        },
        fileList: [],
        form: {
          taskId: "",
          sendId: "",
          finishFlag: "0",
          content: "",
          maintPic: "",
        },
        action: CONFIG.baseUrl + "/maint/uploadFile",
        header: {
          token: uni.getStorageSync("token"),
        },
        formData: {
          fileKey: "maint-record",
        },
        resultList: [
          {
            title: "未完成",
            name: "0"
          },
          {
            title: "维修完成",
            name: "1"
          }
        ],
        imageUrl: []
      }
    },
    onLoad(options) {
      this.form.taskId = options.taskId;
      this.form.sendId = options.sendId;
    },
    methods: {
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      onSuccess(data, index, lists, name) {
        if (data.code === 200 && data.data) {
          this.imageUrl.push(data.data);
        }
      },
      onRemove(index, lists, name) {
        this.imageUrl.splice(index, 1);
      },
      resultChange() {},
      submitForm() {
        if(this.imageUrl.length) {
          this.form.maintPic = this.imageUrl.join(',');
        }

        const finalForm = Object.assign({}, this.form);
        this.loading = true;
        this.$u
            .post('/maint/saveMaintRecord', finalForm)
            .then(res => {
              if (res.code === 200) {
                this.$refs.uToast.show({
                  title: res.message,
                  type: 'success'
                });
                setTimeout(() => {
                  if(finalForm.finishFlag === '0') {
                    this.back();
                  } else {
                    uni.reLaunch({
                      url: "/pages/home/index"
                    });
                  }
                }, 300)
              }
            }).catch(e => {
        }).finally(_ => {
          this.loading = false;
          this.$store.commit("freshTaskFunc", true);
        })
      },
    }
  }
</script>

<style scoped lang="scss">
  .add-btn {
    margin-top: 10px;
    width: 50px;
    height: 50px;
    border-radius: 5px;
    border: 1px dashed #f1f1f1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>